import { Component, OnDestroy, OnInit } from '@angular/core';
import { BasePage } from '../../base/base.page';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';

import { Observable, Subscription } from 'rxjs';
import { JobOffersState } from 'src/app/@store/job-offers/job-offers.state';
import { AcademicProgramsState } from 'src/app/@store/academic-programs/academic-programs.state';
import { IAcademicProgram } from '../../@store/academic-programs/academic-programs.model';
import { SetCurrentAcademicProgram } from '../../@store/academic-programs/academic-programs.actions';

@Component({
  selector: 'app-see-all',
  templateUrl: './see-all.page.html',
  styleUrls: ['./see-all.page.scss'],
})
export class SeeAllPage extends BasePage implements OnInit, OnDestroy {
  private suggestionsSubscription: Subscription;
  public items = [];

  constructor(router: Router, location: Location, store: Store, utilsService: UtilsService) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {
    this.subscribeToSuggestions();
  }

  public onItemClick($event) {
    const program = this.store
      .selectSnapshot(AcademicProgramsState.getAcademicProgramsSuggestions)
      .find((item) => item.id === $event);
    const programUpdated: IAcademicProgram = { ...program, own: false } as IAcademicProgram;
    this.store.dispatch(new SetCurrentAcademicProgram(programUpdated));
    this.router.navigate(['academic-program-detail', $event]);
  }

  private subscribeToSuggestions() {
    this.suggestionsSubscription = this.store
      .select(AcademicProgramsState.getAcademicProgramsSuggestions)
      .subscribe((res) => {
        this.items = res;
      });
  }

  private disconnectObservers(): void {
    if (this.suggestionsSubscription) {
      this.suggestionsSubscription.unsubscribe();
    }
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
